import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        announcements: {
            cat: ""
        }
    },
    getters: {},
    mutations: {
        changeAnnouncmentsCat(state, payload) {
            state.announcements.cat = payload
        }
    },
    actions: {}
});