<template>
  <v-card outlined>
    <v-skeleton-loader
      :loading="loading"
      :transition="transition"
      height="94"
      type="list-item-two-line"
    >
      <v-card-actions align>
        <div v-if="categories && categories.length">
          <v-btn
            text
            :outlined="activeCat === '' ? true : false"
            key
            color="primary"
            append
            style="text-transform: none;"
            @click.native="updateCat('')"
          >Όλοι οι πινακες</v-btn>
          <v-btn
            v-for="categorie in categories"
            v-bind:key="categorie._id"
            :outlined="categorie._id == activeCat ? true : false"
            text
            color="primary"
            append
            style="text-transform: none;"
            @click.native="updateCat(categorie._id)"
          >{{ categorie.name }}</v-btn>
        </div>
        <v-alert v-if="errors && errors.length" prominent type="error">
          <v-row align="center" v-bind:key="error.code" v-for="error in errors" >
            <v-col class="grow" >{{ error.message }}</v-col>
            <v-col class="shrink"></v-col>
          </v-row>
        </v-alert>
      </v-card-actions>
    </v-skeleton-loader>
  </v-card>
</template>

<script>
import { HTTP } from "../../api";

export default {
  data() {
    return {
      loading: true,
      transition: "scale-transition",
      categories: [],
      errors: []
    };
  },
  computed: {
    activeCat: function() {
      return this.$store.state.announcements.cat;
    }
  },
  created() {
    this.loading = true;
    HTTP.get("categories/public", {
      params: {
        q: {}
      }
    })
      .then(response => {
        this.loading = false;
        this.categories = response.data;
      })
      .catch(e => {
        this.loading = false;
        this.errors.push(e.response.data);
      });
  },
  methods: {
    updateCat(x) {
      this.$store.commit("changeAnnouncmentsCat", x);
    }
  }
};
</script>