<template>
  <v-skeleton-loader v-bind:loading="loading" type="list-item-two-line">
    <v-data-table
      v-bind="$props"
      :headers="headers"
      :items="items"
      item-key="_id"
      wrap
    >
      <template slot="item" slot-scope="props" >
        <tr @click="clickRow(props.item)">
          <td class="text-start">{{ props.item._about }}</td>
          <td class="text-start">{{ props.item.publisher.name }}</td>
          <td class="text-start">{{ getDate(props.item.date) }}</td>
          <td class="text-start">{{ props.item.title }}</td>
          <td class="text-start">
            <v-btn
              v-for="attachment in props.item.attachments"
              v-bind:key="attachment"
              text
              fab
              x-small
              color="primary"
              append
              :href="viewFile(attachment)"
              target="_blank"
              style="text-transform: none;"
            >
              <v-icon>mdi-file-eye-outline</v-icon>
            </v-btn>
            <v-btn
              v-if="props.item.attachments.length > 1"
              text
              fab
              x-small
              color="primary"
              append
              :href="downloadAll(props.item._id)"
              style="text-transform: none;"
            >
              <v-icon>mdi-folder-download</v-icon>
            </v-btn>
          </td>
        </tr>
      </template>
    </v-data-table>
  </v-skeleton-loader>
</template>

<script>
import { HTTP, baseURL } from "../../api";
import moment from "moment";

export default {
  props: {
    about: String,
    psize: Number
  },
  watch: {
    about: function() {
      this.getData();
    }
  },
  created: function() {
    this.getData();
  },
  methods: {
    getData: function() {
      this.loading = true;
      let q = {};
      let pageSize = this.$props.psize ? this.$props.psize : 0;
      if (this.$props.about)
        q = {
          _about: this.$props.about
        };

      HTTP.get("announcements/public", {
        params: {
          q: q,
          pageSize: pageSize
        }
      })
        .then(response => {
          this.loading = false;
          this.items = response.data;
        })
        .catch(e => {
          this.loading = false;
          this.items = [];
        });
    },
    getDate: function(x) {
      return moment(x).format("D/M/YYYY HH:mm");
    },
    downloadAll: function(x) {
      return baseURL + "files/" + x + "/downloadAll";
    },
    viewFile: function(x) {
      return baseURL + "files/" + x + "/view";
    },
    clickRow: function(item) {
      this.$router.push({ path: '/announcement/'+item._id })
    }
  },
  data() {
    return {
      loading: true,
      expanded: [],
      search: "",
      singleExpand: false,
      headers: [
        { text: "Πίνακας", value: "_about" },
        { text: "Υποβλήθηκε από", value: "publisher.name" },
        { text: "Προστέθηκε", value: "date" },
        { text: "Τίτλος", value: "title" },
        { text: "Επισύναψη", value: "-" }
      ],
      items: []
    };
  },
  computed: {}
};
</script>

<style>
    table tbody tr {
        cursor: pointer;
    }

</style>