 <template>
  <v-alert prominent :type="classType">
    <v-row align="center">
      <v-col class="grow">{{ message }}
      </v-col>
    </v-row>
  </v-alert>
</template>

<script>
export default {
  data: () => ({
    //
  }),
  props: {
    status: Number,
    message: String,
    code: Number
  },
  computed: {
    classType: function() {
      if (this.status > 401) return "error";
      else return "warning";
    }
  }
};
</script>