<template>
  <v-skeleton-loader
    :loading="loading"
    :transition-group="transition"
    height="94"
    type="list-item-two-line"
  >
    <template v-if="errors && errors.length > 0 && !loading">
      <v-layout row>
        {{ errors }}
        <v-col sm="12" v-if="errors.length > 0">
          <v-card v-for="e in errors" :key="e.status" class="mx-auto">
            <ErrorAlert
              :status="e.status"
              :message="e.data.error.message"
              :code="e.data.error.code"
            />
          </v-card>
        </v-col>
      </v-layout>
    </template>
    <v-layout row>
      <v-col sm="10" v-if="announcement.title">
        <v-card class="mx-auto">
          <v-list-item three-line>
            <v-list-item-avatar color="grey">
              <v-img :src="userImage"></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title class="headline">{{ announcement.title }}</v-list-item-title>
              <v-list-item-subtitle>από {{ announcement.publisher.name }}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-card-text
            style="word-wrap: break-word;white-space:pre-wrap"
            class="gray--text text--darken-1"
          >{{ announcement.text }}</v-card-text>

          <v-card-actions clas="text-center">
            <v-spacer></v-spacer>
            <v-btn small @click="goBack()">
              <v-icon small>mdi-arrow-left-bold-circle</v-icon>επιστροφη
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn icon>
              <v-icon>mdi-heart</v-icon>
            </v-btn>
            <v-btn icon>
              <v-icon>mdi-share-variant</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-layout>
  </v-skeleton-loader>
</template>

<script>
import { HTTP, baseURL } from "../../api";
import moment from "moment";

import ErrorAlert from "@/components/errors/errorAlert.vue";

export default {
  components: {
    ErrorAlert
  },
  created: function() {
    this.errors = [];
    this.getData(this.$router.currentRoute.params.id);
  },
  methods: {
    getData: function(id) {
      this.loading = true;
      HTTP.get("announcements/" + id, {
        params: {}
      })
        .then(response => {
          this.loading = false;
          let r = response.data;
          this.announcement = r;
          this.userImage = this.getUserImage(r.publisher.id);
        })
        .catch(e => {
          this.loading = false;
          let r = e;
          this.errors.push(r.response);
        });
    },
    getDate: function(x) {
      return moment(x).format("D/M/YYYY HH:mm");
    },
    goBack() {
      return this.$router.go(-1);
    }
  },
  data() {
    return {
      loading: true,
      transition: "scale-transition",
      announcement: [],
      errors: []
    };
  },
  computed: {
    userImage: function() {
      return baseURL + "user/image/" + this.announcement.publisher.id;
    }
  }
};
</script>